.contentblock-Carousel {

    .image-slider {
        img {
            display: block;
            max-width: 100%;
            margin: 0;
        }

        .slick-slide {
            outline: none !important;
        }

        .slick-dots {
            position: relative;
            list-style: none;
            margin: 1rem 0;
            padding: 0;
            text-align: center;

            li {
                margin: 5px;
                display: inline-block;

                &.slick-active {
                    button {
                        background: #000;
                    }
                }
            }

            button {
                border: none;
                width: 8px;
                height: 8px;
                text-indent: -9999em;
                display: block;
                border-radius: 50%;
                margin: 0;
                padding: 0;
                background: #d7d7d7;
            }
        }
    }
}


