/// <reference path="../variables.scss" />


.contentblock-TextAndImage {

    .text-and-image {
        display: flex;

        &.swapped {
            .col-text {
                order: 2;
            }

            .col-img {
                order: 1;
            }
        }

        .col-text {
            .text {
                padding: 5rem 5rem 5rem 0;
                margin-bottom: 0;

                h1, h2, h3, h4, h5, h6 {
                    &:first-of-type {
                        margin-top: 0;
                    }
                }
            }
        }

        &.swapped {
            .col-text .text {
                padding: 5rem 0 5rem 5rem;
            }
        }

        .col-img {
            display: flex;

            figure {
                width: 100%;
            }

            img {
                display: block;
                max-width: 100%;
            }
        }
    }
}


@include media(md) {
}

@include media(sm) {
    .contentblock-TextAndTwoColumn {
        .text-and-image {
            display: flex;
            flex-wrap: wrap;
        }
    }
}

@include media(xs) {
}
