﻿@import 'mixins';

/* Fonts */
$font-fontawesome: "Font Awesome 5 Free";
$font-raleway: "Raleway", Arial, "Helvetica Neue", Helvetica, sans-serif;
$font-cardo: "Cardo", serif, $font-raleway;

$font-content: $font-raleway;
$font-title: $font-cardo;

/* Colors */
$color-text: #414141;

/* Grid */
$gutter: 1rem;

/* Breakpoints */
$screen-lg: 1280px;
$screen-md: 1024px;
$screen-sm: 768px;
$screen-xs: 480px;