/********************************
    AT POPUP
*********************************/
#iframeAT {
    min-height: 100vh;
    width: 90%;
    max-width: 800px;
    background: #fff;
    position: absolute;
    left: -830px;
    top: 0;
    z-index: 999;
    transition: all ease-in-out .3s;

    &.active {
        left: 0;
    }

    .iframeAT-sroll {
        height: 100%;
        width: 100%;
        /*overflow-y: scroll;
        -webkit-overflow-scrolling: touch;*/

        &::-webkit-scrollbar {
            width: 0;
        }
    }

    #iframeContainer {
        height: 100%;
        width: 100%;
    }

    iframe {
        min-height: 100vh;
        height: 100%;
        border: none;
        min-width: 100%;
        width: 100%;
    }
}

.iframeAT-background {
    width: 100vw;
    height: 100vh;
    background: rgba(0,0,0,0.7);
    position: fixed;
    left: 0;
    top: 0;
    opacity: 0;
    pointer-events: none;
    z-index: 998;
    transition: all ease-in-out .3s;
    display: flex;
    justify-content: center;
    align-items: center;
}


.at-active .iframeAT-background {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
}


.at-loading .iframeAT-background {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
}

body.at-active {
    overflow-y: hidden;
    width: 100%;
}

.ticketATClose {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    background: #000;
    position: absolute;
    right: -25px;
    top: 10px;
    transition: .2s all ease;

    &:hover {
        transform: rotate(90deg);
    }

    span {
        background: white;
        height: 2px;
        width: 15px;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(45deg);

        &:first-of-type {
            transform: translate(-50%, -50%) rotate(-45deg);
        }
    }
}

/* Loading animation */

.at-loading .lds-ellipsis {
    opacity: 1;
}

.lds-ellipsis {
    transition: all ease-in-out .3s;
    opacity: 0;
    display: inline-block;
    position: relative;
    width: 64px;
    height: 64px;
}

.lds-ellipsis div {
    position: absolute;
    top: 27px;
    width: 11px;
    height: 11px;
    border-radius: 50%;
    background: #fff;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}

.lds-ellipsis div:nth-child(1) {
    left: 6px;
    animation: lds-ellipsis1 0.6s infinite;
}

.lds-ellipsis div:nth-child(2) {
    left: 6px;
    animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(3) {
    left: 26px;
    animation: lds-ellipsis2 0.6s infinite;
}

.lds-ellipsis div:nth-child(4) {
    left: 45px;
    animation: lds-ellipsis3 0.6s infinite;
}

@keyframes lds-ellipsis1 {
    0% {
        transform: scale(0);
    }

    100% {
        transform: scale(1);
    }
}

@keyframes lds-ellipsis3 {
    0% {
        transform: scale(1);
    }

    100% {
        transform: scale(0);
    }
}

@keyframes lds-ellipsis2 {
    0% {
        transform: translate(0, 0);
    }

    100% {
        transform: translate(19px, 0);
    }
}
