﻿/// <reference path="mixins.scss" />
/// <reference path="variables.scss" />

a {
    color: #000;
    text-decoration: underline;
    outline: none;

    &:hover,
    &:active,
    &:focus {
        color: $color-text;
        text-decoration: none;
        outline: none;
    }
}

.content {

    h1, h2, h3 {
        font-family: $font-title;
    }

    h1 {
        font-size: 4.8rem;
        line-height: 1.25;
        margin: 0;
    }

    h2 {
        font-size: 3.8rem;
        line-height: 1.25;
        margin: 0;
    }

    h3 {
        font-size: 2.2rem;
        line-height: 1.25;
        margin: 0;
    }

    h4, h5, h6 {
        font-size: 1.6rem;
        font-weight: bold;
        line-height: 1.25;
        margin: 0;
    }

    p {
        margin: 0 0 1.5em 0;
        padding: 0;
    }
}

.align-right {
    text-align: right;
}

.align-center {
    text-align: center;
}

.hidden {
    display: none;
}


/* Buttons */

.btn {
    display: inline-block;
    padding: 2.3rem;
    font-size: 1.4rem;
    font-weight: 700;
    text-decoration: none;
    cursor: pointer;
    border: none;
}

.btn-primary {
    background: #000;
    color: #fff;
    text-align: center;
    pointer-events: all;

    &:hover {
        background: #555;
        color: #fff;
    }
}

.btn-secondary {
    background: #555;
    color: #fff;
    text-align: center;

    &:hover {
        background: #000;
        color: #fff;
    }
}


/* Background image */
.bg-image {
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;

    &.left-top {
        background-position: left top !important;

        .bg-image-img {
            object-position: left top;
        }
    }

    &.center-top {
        background-position: center top !important;

        .bg-image-img {
            object-position: center top;
        }
    }

    &.right-top {
        background-position: right top !important;

        .bg-image-img {
            object-position: right top;
        }
    }

    &.left-center {
        background-position: left center !important;

        .bg-image-img {
            object-position: left center;
        }
    }

    &.center-center {
        background-position: center center !important;

        .bg-image-img {
            object-position: center center;
        }
    }

    &.right-center {
        background-position: right center !important;

        .bg-image-img {
            object-position: right center;
        }
    }

    &.left-bottom {
        background-position: left bottom !important;

        .bg-image-img {
            object-position: left bottom;
        }
    }

    &.center-bottom {
        background-position: center bottom !important;

        .bg-image-img {
            object-position: center bottom;
        }
    }

    &.right-bottom {
        background-position: right bottom !important;

        .bg-image-img {
            object-position: right bottom;
        }
    }

    .bg-image-img {
        object-fit: cover;
        width: 100%;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: 0;
    }

    > * {
        position: relative;
        z-index: 1;
    }
}


@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ CSS styles go here */

    /* Background image */
    .bg-image {
        .bg-image-img {
            display: none !important;
        }
    }
}