﻿/// <reference path="../mixins.scss" />
/// <reference path="../variables.scss" />


.contentblock-TextTwoColumn {
    .multicolumn {
        .col-1-2 {
            &:nth-child(1) {
                .text {
                    padding-right: 1rem;
                }
            }

            &:nth-child(2) {
                .text {
                    padding-left: 1rem;
                }
            }
        }
    }
}


@include media(md) {
}

@include media(sm) {
}

@include media(xs) {

    .contentblock-TextTwoColumn {
        .multicolumn {
            .col-1-2 {
                &:nth-child(1) {
                    .text {
                        padding-right: 0;
                    }
                }

                &:nth-child(2) {
                    .text {
                        padding-left: 0;
                    }
                }
            }
        }
    }
}