﻿/// <reference path="mixins.scss" />
/// <reference path="variables.scss" />

*,
*:after,
*:before {
    box-sizing: border-box;
    outline: none;
}
html {
    font-size: 62.5%;
    font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    overflow-x: hidden;
}
body {
    font-size: 1.6rem;
    background-color: #fff;
    font-family: $font-content;
    line-height: 1.4;
    font-weight: 400;
    color: $color-text;
    /*overflow-x: hidden;*/ /* turned off to use position: sticky in timetable */
    min-height: 100vh;
}



/**
 * Fade-zoom animation for first dialog
 */
.mfp-zoom-out .zoom-anim-dialog {
    opacity: 0;
    transition: all .5s cubic-bezier(0.455, 0.030, 0.515, 0.955);
    transform: scale(1.3);
}

.mfp-zoom-out.mfp-ready .zoom-anim-dialog {
    opacity: 1;
    transform: scale(1);
}

.mfp-zoom-out.mfp-removing .zoom-anim-dialog {
    transform: scale(1.3);
    opacity: 0;
}

.mfp-zoom-out.mfp-bg {
    opacity: 0;
    transition: all .5s cubic-bezier(0.455, 0.030, 0.515, 0.955);
}

.mfp-zoom-out.mfp-ready.mfp-bg {
    opacity: 0.6;
}

.mfp-zoom-out.mfp-removing.mfp-bg {
    opacity: 0;
}

/**
 * Simple fade transition,
 */
.mfp-fade.mfp-bg {
    opacity: 0;
    -webkit-transition: all 0.15s ease-out;
    -moz-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out;
}

.mfp-fade.mfp-bg.mfp-ready {
    opacity: 0.8;
}

.mfp-fade.mfp-bg.mfp-removing {
    opacity: 0;
}

.mfp-fade.mfp-wrap .mfp-content {
    opacity: 0;
    -webkit-transition: all 0.15s ease-out;
    -moz-transition: all 0.15s ease-out;
    transition: all 0.15s ease-out;
}

.mfp-fade.mfp-wrap.mfp-ready .mfp-content {
    opacity: 1;
}

.mfp-fade.mfp-wrap.mfp-removing .mfp-content {
    opacity: 0;
}







