.contentblock-Gallery {

    .image-gallery {
        .item {
            display: block;
            float: left;
            padding: 3px;
            width: 33.33%;
            cursor: pointer;

            &:hover {
                opacity: .8;
            }
        }

        &.two-columns {
            .item {
                width: 50%;
            }
        }

        &.four-columns {
            .item {
                width: 25%;
            }
        }

        .item img {
            outline: none;
            border: none;
            display: block;
            max-width: 100%;
        }
    }
}


