﻿/// <reference path="../mixins.scss" />
/// <reference path="../variables.scss" />


.contentblock-RelatedItems {

    .section-title {
        padding: 0 0 2rem 0;
        margin: 0;
    }

    .post-wrapper:not(.flickity-enabled) {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }

    .post {
        margin-bottom: 30px;

        &:hover {

            a {
                color: #000;
            }
        }

        .post-image {
            display: block;
            color: $color-text;
            margin-bottom: 2rem;

            &.no-image {
                figure {
                    background-color: #f1f1f1;
                    background-size: 39px;
                    background-repeat: no-repeat;
                    background-position: center center;
                }
            }

            &:hover {
                figure img {
                    opacity: 0.8;
                }
            }

            figure {
                display: block;
                width: 100%;
                overflow: hidden;
                position: relative;
                padding-bottom: 56%;
                background: #000;
            }

            .labels {
                margin: 0;
                padding: 0;
                list-style: none;
                position: absolute;
                bottom: 0;
                left: 0;

                li {
                    display: block;
                }

                .label {
                    display: inline-block;
                    background: #fff;
                    color: #000;

                    &.main {
                        background: #000;
                        color: #fff;
                    }
                }
            }



            img {
                display: block;
                width: 100%;
                height: auto;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                bottom: 0;
                left: 0;
            }
        }

        .datetime {
            font-size: 1.3rem;
            line-height: 1.4rem;
            margin-bottom: .6rem;

            .date {
                display: inline-block;
                margin-right: 10px;
            }

            .time {
                display: inline-block;
            }
        }

        a {
            color: $color-text;
            text-decoration: none;
        }

        .title {
            margin: 0 0 1.2rem 0;
            padding: 0;
        }

        .content {
        }
    }

    .post-slider,
    .sm-post-slider {

        .slick-dots {
            list-style: none;
            margin: 5rem auto;

            li {
                display: inline-block;
                margin: 2px;
            }
        }

        .flickity-page-dots {
            counter-reset: section;
            list-style: none;
            margin: 0;
            position: static;

            .dot {
                display: inline-block;
                text-align: center;
                margin: 0.2rem;
                background: none;
                border: none;
                position: relative;
                opacity: 1;
                height: 3rem;
                width: 3rem;
                line-height: 3rem;

                &::before {
                    counter-increment: section;
                    content: counter(section);
                }

                &.is-selected,
                &:hover {
                    font-weight: 700;
                }
            }
        }
    }
}


@include media(md) {
}

@include media(sm) {
}

@include media(xs) {

    .contentblock-RelatedItems {
        .post-slider,
        .sm-post-slider {
            &.flickity-enabled {
                .xs-col-1-1 {
                    width: 80%;
                    padding-left: 10px;
                    padding-right: 10px;
                }
            }

            .flickity-page-dots {
                display: none;
            }
        }
    }
}
