.contentblock-Testimonial {
    padding: 3rem 0;

    .quote {
        font-size: 2.8rem;
        font-weight: 300;
    }

    .quote-name {
        display: block;
        font-size: 1.7rem;
        font-weight: 400;
        padding-top: 1.5rem;
    }

    #testimonialpaging {
        position: absolute;
        z-index: 0;
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        max-width: 1140px;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        padding: 2rem 2rem 2rem 2rem;

        .slick-dots {
            display: flex;
            list-style: none;
            margin: 0;
            padding: 0;
            align-items: center;

            button {
                border: none;
                width: 5px;
                height: 5px;
                text-indent: -9999em;
                display: block;
                border-radius: 50%;
                margin: 0;
                padding: 0;
                background: #191c23;
                opacity: .4;
                cursor: pointer;
            }

            li {
                margin: 4px;
                display: inline-block;

                &.slick-active button {
                    background: #191c23;
                    opacity: 1;
                }
            }
        }
    }
}


