.contentblock-Document {

    .downloads {
        ul {
            list-style: none;
            margin: 0;
            padding: 0;

            li {
                display: block;
            }

            a {
                display: block;
                border-bottom: 1px solid #000;
                text-decoration: none;
                padding: 2rem 10rem 2rem 8rem;
                position: relative;
            }

            a:hover {
                .filetype,
                .filedownload {
                    background: #555;
                    color: #fff;
                }
            }
        }

        .filetype {
            display: inline-block;
            background-color: #000;
            color: #fff;
            padding: .8rem 1.4rem;
            position: absolute;
            left: 2rem;
            top: 50%;
            transform: translateY(-50%);
        }

        .filetitle {
            display: inline-block;
            font-size: 1.7rem;
            padding: 0 3rem;
        }

        .filedownload {
            display: inline-block;
            width: 4rem;
            height: 4rem;
            text-align: center;
            line-height: 4.3rem;
            border-radius: 50%;
            background: #000;
            color: #fff;
            position: absolute;
            right: 2rem;
            top: 50%;
            transform: translateY(-50%);

            .icon {
                font-size: 1.8rem;
                width: 1.8rem;
                height: 1.8rem;
            }
        }
    }
}


