﻿input,  
textarea {
    @include input-placeholder {
        color: #888;
    }
}

.styled-form {
    .form-row {
        margin-bottom: 1.5rem;

        label {
            width: 100%;
            display: block;
            margin-bottom: .5rem;
        }

        input[type="text"],
        input[type="date"],
        input[type="email"],
        input[type="password"],
        select {
            border: 1px solid;
            width: 100%;
            padding: 1.5rem 1rem;
            font-size: 1.6rem;

            &.input-validation-error {
                background: #ffe7e7;
            }
        }

        textarea {
            border: 1px solid;
            height: 17.5rem;
            width: 100%;
            padding: 10px;
            outline: none;
            font-size: 14px;
            resize: none;

            &.input-validation-error {
                background: #ffe7e7;
            }
        }

        .field-validation-valid {
            display: none;
        }

        .field-validation-error {
            font-size: 1.2rem;
            color: #CC3333;
            display: block;
        }

        .fileUpload {
            position: relative;
            overflow: hidden;

            input[type=file] {
                position: absolute;
                top: 0;
                right: 0;
                margin: 0;
                padding: 0;
                font-size: 2rem;
                cursor: pointer;
                opacity: 0;
                filter: alpha(opacity=0);
            }
        }

        .delete-file {
            position: absolute;
            right: 12px;
            top: 9px;
        }

        .checkbox {
            margin: 0;
            display: inline;
            padding: 1px 1rem 0 1px;

            label {
                display: inline-block;
                width: auto;
            }

            input[type="checkbox"] {
                float: left;
                margin-right: .5rem;
                position: relative;
                top: .6rem;
            }

            input[type="radio"] {
                position: relative;
                top: .4rem;
                margin-right: .5rem;
            }
        }

        .radio {
            margin: 0;
            display: inline-block;
            padding: .8rem 0 0;

            label {
                display: inline-block;
                width: auto;
            }

            input[type="checkbox"] {
                position: relative;
                top: .4rem;
                margin-right: .5rem;
            }

            input[type="radio"] {
                position: relative;
                top: 1px;
                margin-right: .5rem;
            }
        }
    }
}
