﻿.preview-dialog {
    position: fixed;
    bottom: 2rem;
    left: 2rem;
    background-color: rgba(255, 106, 0, 0.5);
    border: 1px solid #808080;
    box-shadow: rgba(0,0,0,0.3) 2px 2px 10px;

    h4 {
        display: block;
        width: 100%;
        padding: 0 2rem;
    }

    .preview-false {
        width: 100%;
        background: grey;
        display: block;
        text-align: center;
        text-decoration: none;
    }
}
