.contentblock-Header {

    &.single-hero {
        height: 100vh;
        display: flex;
        position: relative;
        background: #000;
        margin-bottom: 8rem;
        color: white;

        figure {
            width: 100%;
            display: flex;
        }


        .slider-content {
            padding-bottom: 10rem;
            display: flex;
            justify-content: space-between;
            align-content: center;
            align-items: flex-end;
            flex-wrap: wrap;

            .slider-button {
                display: flex;
                justify-content: flex-end;
                flex-wrap: wrap;
            }
        }

        .grid {
            position: relative;
            z-index: 5;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            flex-direction: column;
            backface-visibility: hidden;
        }

        .text {
            .title {
                font-size: 4.2rem;
                font-weight: 300;
            }
        }

        span {
            font-size: 1.4rem;
            font-weight: 400;
            line-height: 1.2;
        }

        .hero-video {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background-position: center center;
            background-size: cover;
            background-color: #f1f1f1;
            overflow: hidden;
            display: flex;
            align-items: center;

            .video-container {
                width: 100%;
                margin: auto;
                position: relative;
                z-index: 1;
                height: 100%;
                display: -webkit-box;
                display: -ms-flexbox;
                display: flex;
                align-items: center;

                video {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    width: auto;
                    min-width: 100%;
                    height: auto;
                    min-height: 100%;
                    transform: translateX(-50%) translateY(-50%);
                }
            }
        }
    }
}


@include media(md) {
}

@include media(sm) {
    .contentblock-Header {
        &.single-hero {
            .slider-content {
                .slider-button {
                    justify-content: flex-start;
                    padding-top: 2rem;
                }
            }
        }
    }
}

@include media(xs) {
}
