.contentblock-Faq {

    .faq {
        border-top: 3px solid #121212;

        .faq-item {
            border-bottom: 1px solid;

            .faq-title {
                font-size: 1.7rem;
                font-weight: 400;
                position: relative;
                margin: 0;
                padding: 3rem 5rem 3rem 0;
                cursor: pointer;
                transition: all .2s cubic-bezier(0.455, 0.030, 0.515, 0.955);

                &:hover {
                    color: #000;
                }

                &.active {
                    font-weight: 700;

                    .svg-inline--fa {
                        transform: rotate(180deg) translateY(50%);
                    }
                }

                .svg-inline--fa {
                    position: absolute;
                    right: 3rem;
                    top: 50%;
                    transform: translateY(-50%);
                    font-weight: normal;
                    transition: all .2s cubic-bezier(0.455, 0.030, 0.515, 0.955);
                }
            }

            .faq-text {
                display: none;
                font-weight: 300;
                font-size: 1.5rem;
                line-height: 2.4rem;
            }
        }
    }
}


