.contentblock-Quote {

    .quote {
        text-align: center;
        padding: 5rem;
        outline: none;
        background-color: #000;

        &.has-bg {
            background-size: cover;
            background-position: center center;
            color: #fff;
        }

        .title {
            display: block;
            color: #fff;
        }

        .rating {
            margin: 0;
            padding: 0;
            list-style: none;
            color: #fff;
            font-weight: normal;

            li {
                display: inline-block;
                margin: 1px;
            }
        }

        .name {
            display: block;
            color: #fff;
        }
    }

    .quote-slider {
        position: relative;

        .slick-track {
            display: flex;

            .slick-slide {
                display: flex;
                flex-direction: column;
                height: auto;

                & > div {
                    display: flex;
                    width: 100%;
                }
            }
        }

        .slick-dots {
            position: relative;
            list-style: none;
            margin: 0;
            padding: 1rem 0;
            text-align: center;
            z-index: 9;

            li {
                margin: 5px;
                display: inline-block;

                &.slick-active {
                    button {
                        background: #000;
                    }
                }
            }

            button {
                border: none;
                width: 8px;
                height: 8px;
                text-indent: -9999em;
                display: block;
                border-radius: 50%;
                margin: 0;
                padding: 0;
                background: #d7d7d7;
            }
        }
    }
}


