.contentblock-Image {

    .image-list {
        img {
            display: block;
            max-width: 100%;
            margin: 0;
        }

        .image {
            position: relative;
            display: inline-block;

            a {
                display: block;
                position: relative;

                &::before {
                    content: "";
                    position: absolute;
                    display: block;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    width: 100%;
                    height: 100%;
                    background: #000;
                    opacity: 0;
                    z-index: 1;
                    transition: cubic-bezier(.115,.56,.45,.995) all .3s;
                }

                &:hover {
                    &::before {
                        opacity: 0.3;
                    }
                }
            }
        }

        .image-title {
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            pointer-events: none;

            &::after {
                content: "";
                position: absolute;
                display: block;
                bottom: 0;
                left: 0;
                right: 0;
                width: 100%;
                height: 100%;
                background-image: linear-gradient(180deg,transparent 0,#000 100%);
                opacity: .8;
                z-index: 1;
            }

            .title {
                margin: 0;
                padding: 2rem;
                font-size: 3.6rem;
                color: #fff;
                position: relative;
                z-index: 2;
            }
        }
    }
}


