﻿/// <reference path="../mixins.scss" />
/// <reference path="../variables.scss" />


.contentblock-TrailerOneColumnAt {

    .page-header-wrapper {
        display: block;
        position: relative;
        width: 100%;
    }

    .page-header-content-wrapper {
        order: 1;
    }

    .page-header-visual-wrapper {
        display: flex;
        order: 2;
    }

    .page-header-content {
        position: relative;
        z-index: 1;
    }

    .labels {
        margin: 0 0 1rem 0;
        padding: 0;
        list-style: none;

        li {
            display: inline-block;
        }

        .label {
            display: inline-block;
            background: #fff;
            color: #000;

            &.main {
                background: #000;
                color: #fff;
            }
        }
    }

    .page-header-visual-inside {
        position: absolute;
        bottom: 0;
        z-index: 10;
        width: 100%;
        color: #fff;
        pointer-events: none;

        .clearbutton {
            color: #fff;
        }

        .page-header-content {
            display: flex;

            [class*='col-'] {
                position: relative;
            }

            .next-show {
                color: #fff;
            }
        }
    }

    .page-header-visual {
        background: #121212;
        width: 100%;
        position: relative;
        overflow: hidden;
        background-size: cover;
        background-position: center center;

        .btn-trailer {
            position: absolute;
            top: 50%;
            left: 50%;
            width: 6.4rem;
            height: 6.4rem;
            background: #000;
            color: #fff;
            border-radius: 50%;
            z-index: 110;
            transform: translate(-50%, -50%);
            text-decoration: none;

            .fa-play {
                position: absolute;
                top: 50%;
                left: 53%;
                transform: translate(-50%, -50%);
                font-size: 2.2rem;
                width: 2.2rem;
                height: 2.2rem;
            }

            &:hover {
                background: #fff;
                color: #000;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100%;
            display: block;
            z-index: 10;
        }

        .tv {
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: 100%;
            overflow: hidden;
            padding-bottom: 40%;

            .gradient {
                content: "";
                position: absolute;
                display: block;
                bottom: 0;
                left: 0;
                right: 0;
                width: 100%;
                height: 35%;
                background-image: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%);
                opacity: 0.8;
                z-index: 1;
            }

            .screen {
                box-sizing: border-box;
                height: 56.25vw;
                left: 50%;
                min-height: 100%;
                min-width: 100%;
                transform: translate(-50%, -50%) scale(1.2);
                position: absolute;
                top: 50%;
                width: 177.77777778vh;
            }
        }
    }

    .page-header-visual {
        .tv {
            position: relative;

            .screen {
                box-sizing: border-box;
                height: 56.25vw;
                left: 50%;
                min-height: 100%;
                min-width: 100%;
                transform: translate(-50%, -50%);
                position: absolute;
                top: 50%;
                width: 100%;
            }
        }
    }
}


@include media(md) {
}

@include media(sm) {

    .contentblock-TrailerOneColumnAt {
        .page-header-wrapper {
            flex-wrap: wrap;
        }

        .page-header-content-wrapper {
            order: 2;
            width: 100%;
        }

        .page-header-visual-wrapper {
            order: 1;
            width: 100%;
        }

        .page-header-visual-inside {
            position: static;
            color: $color-text;
        }
    }
}

@include media(xs) {
}
